import React from 'react'
import styled from 'styled-components'
import { Link } from 'gatsby'

const Navbar = styled.nav`
    height: 100%;
    flex: 1;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
`

const MenuItem = styled(Link)(({ theme }) => `
    // flex: 1;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    height: 100%;
    text-decoration: none;
    text-transform: uppercase;
    color: ${ theme.color.black };
    padding: 1rem 2rem;
    transition: color 250ms, border-bottom-color 250ms;
    background-color: transparent;
    border-top-width: 0.5rem;
    border-top-style: solid;
    border-top-color: transparent;
    border-bottom-width: 0.25rem;
    border-bottom-style: solid;
    border-bottom-color: transparent;
    &:hover {
        color: ${ theme.color.primary.dark };
        border-bottom-color: ${ theme.color.primary.light }33;
    }
    &.active, &.active:hover {
        color: ${ theme.color.primary.dark };
        border-bottom-color: ${ theme.color.primary.main };
    }
`)

export const Menu = ({ items }) => {
    return (
        <Navbar>
            { items.map(item => <MenuItem key={ item.text } to={ item.path } activeClassName="active">{ item.text }</MenuItem>) }
        </Navbar>
    )
}